<template>
  <div>
    <Card style="width: 100%; margin-bottom: 20px">
      <Select
        v-model="selectTpl"
        style="width: 200px; margin-right: 10px"
        @on-change="changeTpl"
      >
        <Option
          v-for="item in tplOptions"
          :value="item.value"
          :key="item.value"
          >{{ item.label }}</Option
        >
      </Select>

      <Button
        shape="circle"
        @click="
          showTpl = true;
          submit(false);
        "
        v-if="!showTpl"
        style="margin-right: 10px"
        icon="ios-create"
        >编辑</Button
      >
      <Button
        shape="circle"
        @click="
          showTpl = false;
          submit(true);
        "
        style="margin-right: 10px"
        icon="ios-eye"
        v-else
        >预览</Button
      >

      <Button shape="circle" @click="saveTplContent" :disabled="!selectTplItem"
        >保存模板</Button
      >
    </Card>

    <Row :gutter="16">
      <Col span="24" v-show="showTpl">
        <Editor
          ref="myEditor"
          :content="tplContent"
          @getData="getTplContentData"
          v-if="tplStatus"
        />
      </Col>

      <Col span="24" v-show="!showTpl">
        <Editor ref="myEditor2" :content="viewContent" v-if="showView" />
      </Col>
    </Row>
  </div>
</template>

<script>
import Editor from "@/components/Editor.vue";
import { tplList, updateTpl } from "@/api/tpl";
import { codeGen } from "@/api/code";
export default {
  data() {
    return {
      showTpl: true,
      tplContent: "",
      viewContent: "",
      showView: true,
      tplStatus: true,

      tplOptions: [],
      selectTpl: undefined,

      // select
      selectTplItem: undefined,
    };
  },
  components: {
    Editor,
  },
  created() {
    this.initTplList();
  },
  methods: {
    saveTplContent() {
      this.selectTplItem.content = this.tplContent;
      updateTpl(this.selectTplItem);
      this.initTplList();
    },
    changeTpl(value) {
      this.tplStatus = false;
      this.showTpl = true;
      this.tplContent = value.content;
      this.$nextTick(() => {
        this.tplStatus = true;
      });
      this.selectTplItem = value;
    },
    initTplList() {
      tplList().then((res) => {
        this.tplOptions = res.data.map((item) => {
          return {
            label: item.tplName,
            value: item,
          };
        });
      });
    },

    getTplContentData(value) {
      console.log("=====!", value);
      this.tplContent = value;
    },
    submit(value) {
      // false 为编辑  true为预览

      if (value) {
        // 预览调用接口
        this.showView = false;
        codeGen({
          content: this.tplContent,
          data: {},
        }).then((res) => {
          let data = res.data;
          this.viewContent = data;
          this.$nextTick(() => {
            this.showView = true;
          });
        });
      }
    },
  },
};
</script>

