<template>
  <Table border :columns="columns" :data="data">
    <template #name="{ row }">
      <strong>{{ row.name }}</strong>
    </template>
    <template #status="{ row, index }">
      <Switch size="large" v-model="row.status" true-value="1" @on-change="changeStatus($event, row)">
        <template #open>
          <span>开启</span>
        </template>
        <template #close>
          <span>关闭</span>
        </template>
      </Switch>
    </template>
    <template #action="{ row, index }">
      <Button
        type="primary"
        size="small"
        style="margin-right: 5px"
        @click="show(index)"
        >View</Button
      >
      <Button type="error" size="small" @click="remove(index)">Delete</Button>
    </template>
  </Table>
</template>
<script>
import { tplList, updateTpl } from "@/api/tpl";
export default {
  data() {
    return {
      columns: [
        {
          title: "模板组名",
          key: "groupName",
        },
        {
          title: "模板名称",
          key: "tplName",
        },
        {
          title: "状态",
          slot: "status",
        },
        {
          title: "模板类型",
          key: "tplType",
        },
        {
          title: "备注",
          key: "remark",
        },
        {
          title: "创建时间",
          key: "createTime",
        },
        {
          title: "创建时间",
          key: "updateTime",
        },
        {
          title: "更新时间",
          slot: "action",
          width: 150,
          align: "center",
        },
      ],
      data: [],
    };
  },
  mounted() {
    this.getData();
  },

  methods: {
    getData() {
      tplList().then((res) => {
        this.data = res.data;
      });
    },

    changeStatus(value, data) {
      console.log(value, data)
      let updateData = {
        ...data,
      };
      console.log(value, data);
      updateData.status = value ? 1 : 0;
      updateTpl(updateData).then(res => {})

      
    },
  },
};
</script>
